import React, { Fragment } from 'react'

const NotFound = () => (
  <Fragment>
    <h1>404: Page not found</h1>
    <p>
      Finding secrets before the game is even here, eh? Come back awhile later and there might be something here.
    </p>
  </Fragment>
)

export default NotFound



